import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
let store = new Vuex.Store({
    state:{
        active:0, //返回键 个人中心 搜索 空 筛选,
        home:0
    },
    mutations:{
        changeActive(state,val){
            state.active = val;
        },
        changeHome(state,val){
            state.home = val;
        },
    },
    getters:{
        getActive(state){
            return state.active;
        },
        getHome(state){
            return state.home;
        },
    },
    actions:{
        setActive({commit},db){
            commit("changeActive",db);
        },
        setHome({commit},db){
            commit("changeHome",db);
        },
    }
})

export default store;


