<template>
    <div id="app" class="app">
        <router-view></router-view>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
// import Edit from "./components/index/index" 
export default {
    name: 'App',
    data(){
        return {

        }
    },
    mounted(){
        let w = document.querySelector("#app").clientWidth;
        window.H = w*1080/2880;
    },
    methods:{
        ...mapActions(['setHome'])
    }
}
</script>

<style>
@import url("../static/css/common.css");
</style>
<style lang="scss" scoped>
.app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>


