<template>
    <div :class="['index-edit',{active:show}]">
        <div class="edit-logo">
            <img :src="logoBg" alt="">
        </div>
        <div class="transform2" :style="{height:sh}">
            <div class="index-edit-box" :style="{height:sh}">
                <div class="edit-box">
                    <img class="edit-box-bg" :src="boxBg" alt="">
                    <div :class="['edit-box-cont',{active:active==index}]" v-for="(item,index) in btns" :key="index">
                        <div class="edit-box-left">
                            <img :src="item.company" alt="">
                        </div>
                        <div class="edit-box-mid">
                            <img :src="item.desc" alt="">
                        </div>
                        <div class="edit-box-right">
                            <img :src="item.img" alt="">
                        </div>
                    </div>
                    <!-- <div :class="['edit-box-cont',{active:isShow}]" v-for="(item,index) in btns" :key="index">
                        <div class="edit-box-left">
                            <img :src="btns[active].company" alt="">
                        </div>
                        <div class="edit-box-mid">
                            <img :src="btns[active].desc" alt="">
                        </div>
                        <div class="edit-box-right">
                            <img :src="btns[active].img" alt="">
                        </div>
                    </div> -->
                </div>
                <div class="edit-handle">
                    <a href="javascript:" class="return-btn" @click.stop="handleReturn">
                        <img :src="returnBg" alt="">
                    </a>
                    <img class="btns-bg" :src="btnBg" alt="">
                    <div class="wrapper">
                        <div  class="content">
                            <a href="javascript:" :class="['item',{active:active==index}]" v-for="(item,index) in btns" :key="index" @click.stop="handleClick($event,index)">
                                <img :src="handleImgUrl(index)" alt="">
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import config from "./config"
import BScroll from 'better-scroll';
try {
    const { ipcRenderer } = require('electron');
} catch (error) {
    
}
export default {
    name:"index-edit",
    props:["show","h","num"],
    data(){
        return {
            // 信息展示背景
            boxBg:`${host}/static/img/index/info/box-bg.png`,
            // logo
            logoBg:`${host}/static/img/index/logo.png`,
            // 按钮底板
            btnBg:`${host}/static/img/index/info/btn-bg.png`,
             // 返回按钮
            returnBg:`${host}/static/img/index/info/return-btn.png`,
            // 按钮
            btns:[],
            active:0,
            swiper:"",
            sub:0,
            // sh
            sh:0
        }
    },
    created(){
        this.btns = config;
    },
    mounted(){
        this.handleInit();
        this.sub=document.querySelector("#app").clientWidth/3840*170;
        this.sh=document.querySelector("#app").clientWidth*1080/3840+'px';
    },
    watch:{
        num(db){
            this.active=db;
        },
        active(db){
            let num = db - 2;
            if(num > 0){
                if(num > 19){
                    this.swiper.scrollTo(-this.sub*19,0,600,0)
                }else{
                    this.swiper.scrollTo(-this.sub*num + this.sub/2,0,600,0)
                }
            }else{
                this.swiper.scrollTo(0,0,600,0)
            }
        }
    },
    methods:{
        handleImgUrl(i){
            return `${host}/static/img/index/nav/${i-0+1}.png`;
        },
        handleInit(){
            this.swiper = new BScroll('.wrapper',{
                scrollX:true,
                click:true,
                tap:true,
                probeType:3,
            });
        },
        handleClick(e,i){
            this.active = i;
        },
        handleReturn(){
            this.$emit("handleIndexOut")
        }
    }
}
</script>
<style lang="scss" scoped>
.index-edit {
    position: absolute;
    width:100%;
    height: 100%;
    top:0;
    left:0;
    opacity: 0;
    overflow: hidden;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    &.active {
        opacity: 1;
        pointer-events: auto;
        .edit-box {
            transition-duration: 600ms;
            opacity: 1;
            transform: matrix(1,0,0,1,0,0);
        }
        .edit-handle {
            transition-duration: 600ms;
            opacity: 1;
            transform: matrix(1,0,0,1,0,0);
        }
    }
    .index-edit-box{
        position: relative;
        width: 100%;
        overflow: hidden;
        display: flex;
        justify-content: center;
    }
    .edit-logo {
        position: absolute;
        top: 0;
        right: 0;
        /* width: 45%; */
        width: 33.645833%;
        img {
            width: 100%;
            transform: translate(300px,-300px);
        }
    }
    .transform2{
        position: relative;
        width: 100%;
    }
    .edit-box {
        position: absolute;
        /* width: 77.152777%; */
        width: 57.864583%;
        height: 70.648148%;
        margin: 0 auto;
        top: 9.814814%;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transform: matrix(1,0,0,1,40,0);
        .edit-box-bg {
            width: 100%;
            height: 100%;
        }
        .edit-box-cont {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: space-around;
            padding: 0 40px;
            .edit-box-left {
                /* width: 18.90189%; */
                /* height: 55.706521%; */
                height: 50%;
                opacity: 0;
                transform: matrix(1,0,0,1,-40,0);
                img {
                    /* max-width: 100%; */
                    height: 100%;
                    margin: 0 auto;
                    &.item-logo {
                        width: 70%;
                        margin: 0 auto 40px;
                    }
                }
            }
            .edit-box-mid {
                width: 43.654365%;
                opacity: 0;
                transform: matrix(1,0,0,1,0,-40);
                img {
                    width: 100%;
                }
            }
            .edit-box-right {
                width: 30.153015%;
                opacity: 0;
                transform: matrix(1,0,0,1,40,0);
                img {
                    width: 100%;
                }
            }
            &.active{
                .edit-box-left,
                .edit-box-mid,
                .edit-box-right {
                    opacity: 1;
                    transition-duration: 600ms;
                    transform: matrix(1,0,0,1,0,0);
                }
            }
        }
    }
    .edit-handle {
        position: absolute;
        /* width: 35.555555%; */
        width: 26.666666%;
        height: 13.333333%;
        bottom: 2.777777%;
        opacity: 0;
        transform: matrix(1,0,0,1,0,40);
        .return-btn {
            position: absolute;
            width: 14.0625%;
            left: -20%;
            top: 26.388888%;
            img {
                width: 100%;
            }
        }
        .btns-bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
        }
        .wrapper {
            position: relative;
            height: 100%;
            z-index: 2;
            overflow: hidden;
            border-radius: 72px;
            .content {
                /* width: 298.828125%; */
                /* width: 315.4296875%; */
                width: 415.0390625%;
                height: 100%;
                display: flex;
                align-items: center;
                flex-wrap: nowrap;
                .item {
                    flex-shrink: 0;
                    flex-grow: 0;
                    height: 90.277777%;
                    margin: 0 .499%;
                    opacity: .5;
                    transition-duration: 600ms;
                    img {
                        height: 100%;
                    }
                    &.active{
                        opacity: 1;
                    }
                }
            }
        }
    }
}
</style>





